:root {
  --primary-main: #52ab98;
  --primary-lighter: #f3f9f8;
  --primary-dark: #00a989;

  --secondary-main: #6076b8;
  --secondary-lighter: #e8ebf5;
  --secondary-dark: #6478b5;

  --text-primary: "#56636F";
  --text-secondary: "#637381";
  --text-textdark: "#212b36";

  --grey-input-border: "#BDBDBD";
  --grey-facebook: "#E4E6EB";
  --grey-input-bg: "#FAFAFA";
  --grey-light: "#F9FBFC";

  --light-color: #f2f4fe;
  --white-color: #ffffff;
}
/* Start Footer */
.main_footer {
  background-color: #f2f2f2;
  padding: 10px 0px;
}
/* End Footer */

.file_uolpad {
  border: 1px solid var(--primary-lighter);
  border-radius: 10px;
  text-align: center;
  padding: 20px;
  width: 110px;
  height: 110px;
}

.file_uolpad img {
  width: 40px;
}
.profile_icon {
  background: var(--white-color);
  padding: 5px;
}
.profile_icon_wrap {
  border: 1px solid var(--primary-lighter);
  padding: 35px;
  border-radius: 100%;
}

.icon_style {
  background-color: #d9d9d9;
  color: var(--text-secondary);
  border-radius: 100%;
  padding: 20px;
}

.footer_logo {
  position: relative;
  top: 0;
  bottom: 0;
  margin: auto !important;
}

.footer_logo :global .MuiBox-root {
  max-width: 100px;
}

header.dashboard_header_wrap {
  background-color: rgb(255 255 255 / 53%);
  -webkit-backdrop-filter: saturate(200%) blur(0.875rem);
  backdrop-filter: saturate(200%) blur(0.875rem);
  box-shadow: none;
}

.profile_steps {
  border-radius: 10px !important;
}

.profile_steps :global .MuiMenuItem-root {
  padding: 12px;
}
.profile_steps:hover :global .MuiMenuItem-root,
.profile_steps:hover :global .MuiListItemIcon-root {
  color: var(--primary-main);
  background-color: var(--primary-lighter);
}

.sidebar_menu {
  color: var(--text-secondary);
}

.sidebar_menu_active :global .MuiListItemText-root .MuiTypography-root,
.sidebar_menu_active :global .MuiSvgIcon-root {
  color: var(--primary-main);
  transition: all 0.5s;
}
.sidebar_menu_active :global .Mui-selected {
  background-color: var(--primary-lighter);
}

.sidebar_menu:hover :global .MuiListItemText-root .MuiTypography-root,
.sidebar_menu:hover :global .MuiSvgIcon-root {
  color: var(--primary-main);
  background-color: var(--primary-lighter);
  transition: all 0.5s;
}

.article_image_upload {
  border: 1px solid #bfc2c8;
  border-radius: 4px;
  padding: 15px;
}

@media only screen and (max-width: 600px) {
  .public_header .header_btn {
    font-size: 14px;
  }
  .radio_option {
    width: 100%;
  }
  .main_footer :global .MuiDivider-root {
    display: none;
  }
}
