@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;200;300;400;500;600;700;800&display=swap');

:root {
  --primary-main: #000000;
  --primary-lighter: #f5cc23;
  --primary-dark: #297b79;

  --secondary-main: #6076b8;
  --secondary-lighter: #e8ebf5;
  --secondary-dark: #6478b5;

  --text-primary: '#56636F';
  --text-secondary: '#637381';
  --text-disabled: '#919EAB';

  --grey-input-border: '#BDBDBD';
  --grey-facebook: '#E4E6EB';
  --grey-input-bg: '#FAFAFA';
  --grey-light: '#F9FBFC';

  --blue-light: #f4f9fc;

  --light-color: #f2f4fe;
  --white-color: #ffffff;
  --black-color: #0c0c0c;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: auto;
  background-color: #f9fafb;
  font-family: 'Noto Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
}

/* Scroll Bar Style Begin */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: #f9fafb;
}

::-webkit-scrollbar-track {
  background-color: #f9fafb;
  border-radius: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(99, 115, 129, 0.48);
  border-radius: 10px;
}

.scrollbar-none {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.scrollbar-none::-webkit-scrollbar {
  display: none;
}

/* Scroll Bar Style End */

.text-white {
  color: #ffffff !important;
}

.bg-white {
  background-color: var(--primary-main);
}

.bg-primary {
  background-color: var(--primary-main);
}

.bg-primary-light {
  background-color: var(--primary-lighter);
}

.text-primary {
  color: var(--primary-main);
}

.text-primary-light {
  color: var(--primary-lighter);
}

.text-primary-light:hover {
  color: var(--black-color);
}

.bg-secondary {
  background-color: var(--secondary-main);
}

.bg-secondary-light {
  background-color: var(--secondary-lighter);
}

.text-secondary-light {
  color: var(--secondary-lighter);
}

.text-secondary-light:hover {
  color: var(--black-color);
}

a {
  color: #637381;
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  color: var(--primary-main);
}

.nav_links {
  font-size: 16px;
  color: #637381;
  text-decoration: none;
}

.nav-links:hover,
.active {
  color: var(--primary-main);
  transition: all 0.5s;
}

.checkbox_wrapper {
  padding: 0 10px;
  border: 1px solid var(--grey-input-border);
  border-radius: 4px;
  background-color: var(--grey-input-bg);
}

.type_active {
  border: 2px solid #000000 !important;
  color: #000000 !important;
}

.navigate_active {
  border: 2px solid #000000 !important;
  border-radius: 50px !important;
  color: #000000 !important;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
  appearance: textfield;
}

.public-DraftStyleDefault-block {
  margin: 0px !important;
}

/* Begin Rich Text Editor Style  */
.custom_color_picker_model > .rdw-colorpicker-modal-options {
  display: grid;
  grid-template-columns: repeat(13, 1fr);
  scrollbar-width: thin;
  scrollbar-color: var(--grey-500) var(--grey-100);
  -ms-overflow-style: none;
}

.custom_color_picker_model > .rdw-colorpicker-modal-options::-webkit-scrollbar {
  display: block;
  width: 6px;
  height: 6px;
}

/* End Rich Text Editor Style  */
/* Start Banner Section */
.center-left {
  top: 50%;
  left: 8%;
  -webkit-transform: translateX(-8%) translateY(-50%);
  transform: translateX(-8%) translateY(-50%);
}

.banner-slider img {
  width: 100%;
  object-fit: cover;
}

.banner-slider .swiper-button-next:after,
.banner-slider .swiper-button-prev:after {
  font-size: 14px;
  font-weight: bold;
  color: var(--black-color);
}

.banner-slider .swiper-button-prev,
.banner-slider .swiper-button-next {
  background-color: var(--white-color);
  width: 50px;
  height: 50px;
  border-radius: 50px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.banner-slider .swiper-button-prev {
  left: 6%;
}

.banner-slider .swiper-button-next {
  right: 6%;
}

/* End Banner Section */

/* Start Service Section */
.service-section {
  background-size: cover !important;
  padding: 8rem 0 8rem 0;
}
